.cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
    font-family: var(--font-family);
}

.cta-content h3 {
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
}

.cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.cta-btn button {
    color: #FFF;
    background: #000;
    border-radius: 40px;
    font-weight: 800;
    font-size: 14px;
    line-height: 30px;
    padding: .5rem 1.5rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width: 650px) {
    .cta {
        flex-direction: column;
    }

    .cta-content h3 {
        margin-top: 1rem;
        font-size: 20px
    }

    .cta-btn {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .cta {
        margin: 2rem 2rem;
        border-radius: 16px;
    }

    .cta-content h3 {
        font-size: 22px;
        line-height: 26px;
    }

    .cta-btn button {
        font-size: 16px;
        line-height: 28px;
        border-radius: 12px;
    }
}