.possibility {
    display: flex;
    flex-direction: row;
    font-family: var(--font-family);
}

.possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.possibility-image img {
    width: 100%;
    height: 100%;
}

.possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.possibility-content h4 {
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
}

.possibility-content h4:last-child {
    color: var(--color-subtext);
}

.possibility-content h1 {  
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}

.possibility-content p {
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
    margin-block: 2rem;
}

@media screen and (max-width: 950px) {
    .possibility {
        flex-direction: column;
    }
    
    .possibility-content h4 {
        font-size: 20px;
        margin-top: 1rem;
    }

    .possibility-content p {
        margin-block: 0;
    }
}

@media screen and (max-width: 550px) {
    .features-heading {
        margin-bottom: 2rem;
    }
}